export default {
	data() {
		return {
			user_token: '',
			MisList: []
		}
	},

	onLoad() {

		uni.showNavigationBarLoading();
		let that = this;
		that.user_token = uni.getStorageSync("user_token");
		uni.request({
			url: that.MisUrl + '/mis/getMisList',
			method: 'POST',
			header: {
				'user_token': that.user_token
			},
			data: {},
			success: res => {
				that.MisList = res.data.result;
				console.log("square###that.MisList: ###" + that.MisList);
				console.log("square###that.MisList: JSON###" + JSON.stringify(that.MisList));
				uni.hideNavigationBarLoading();
			},
			fail: () => {},
			complete: () => {}
		});
	},
	onPullDownRefresh: function() {
		this.upNew();
	},
	methods: {
		upNew() {
			uni.showNavigationBarLoading();
			let that = this;
			that.user_token = uni.getStorageSync("user_token");
			uni.request({
				url: that.MisUrl + '/mis/getMisList',
				method: 'POST',
				header: {
					'user_token': that.user_token
				},
				data: {},
				success: res => {
					that.MisList = res.data.result;
					console.log("square###that.MisList: ###" + that.MisList);
					console.log("square###that.MisList: JSON###" + JSON.stringify(that.MisList));
					uni.hideNavigationBarLoading();
					uni.stopPullDownRefresh();
				},
				fail: () => {},
				complete: () => {}
			});

		},
		toSquareInfo(e) {
			let that = this;
			var item = e.currentTarget.dataset.id;
			console.log(e.currentTarget.dataset.id);
			this.TabCur = e.currentTarget.dataset.id;
			uni.setStorage({
				key: 'square_info',
				data: item
			})
			uni.navigateTo({
				url: 'squareInfo/squareInfo?misId=' + item.misId,
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		toMySquare() {
			uni.navigateTo({
				url: 'mySquare/mySquare',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		toAddSquare() {
			uni.navigateTo({
				url: 'addSquare/addSquare',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		}
	}
}
